@use '../util/' as *;

.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoint(large) {
        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    position: relative;
    background: var(--card-bg);
    color: var(--dark-text1);
    text-align: center;
    padding: rem(24);
    border-radius: rem(5);
    transition: background 200ms ease-in-out;
    cursor: pointer;

    &:hover {
        background: var(--card-hover);
    }

    &--facebook {
        border-top: rem(4) solid var(--facebook);
    }

    &--twitter {
        border-top: rem(4) solid var(--twitter);
    }

    &--instagram {
        padding-top: rem(30);

        &::before {
            content: '';
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            height: rem(4);
            width: 100%;
            background: linear-gradient(225deg,
                    var(--instagram-end)0%,
                    var(--instagram-middle)50.91%,
                    var(--instagram-start)100%);
            border-top-left-radius: rem(5);
            border-top-right-radius: rem(5);
        }
    }

    &--youtube {
        border-top: rem(4) solid var(--youtube);
    }

    &__icon {
        margin-right: rem(5);

        &--facebook {}

        &--twitter {}

        &--instagram {}

        &--youtube {}
    }


    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: rem(28);
        margin-top: rem(5);
        height: rem(20);
    }

    &__subtitle {
        font-size: rem(14);
        font-weight: 700;
        color: var(--text-color2);
    }

    &__username {
        font-size: rem(12);
        font-weight: 700;
        color: var(--text-color2);
    }


    &__followers {
        margin-bottom: rem(25);
    }

    &__count {
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(9);
        color: var(--text-color);

        &--big {
            font-size: rem(56);
        }

        &--small {
            font-size: rem(32);
            font-weight: 700;
        }
    }

    &__label {
        color: var(--text-color2);
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        text-transform: uppercase;
    }

    &__change {
        display: flex;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;
        align-items: center;

        &--up {
            color: var(--greenlime);
        }

        &--down {
            color: var(--brightred);
        }
    }

    &__number {
        margin-left: rem(4);
    }
}